class WebSocketPolyfill {
  constructor(url, protocols) {
    // 
    uni.connectSocket({
      url: url,
      protocols: protocols,
    });

    // 
    uni.onSocketOpen((res) => {
      this.onopen(res);
    });

    // 
    uni.onSocketClose((res) => {
      this.onclose(res);
    });

    // 
    uni.onSocketError((res) => {
      this.onerror(res);
    });

    // 
    uni.onSocketMessage((res) => {
      this.onmessage(res);
    });
  }

  /**
   * 
   */
  onopen(res) {}

  /**
   * 
   */
  onclose(res) {}

  /**
   * 
   */
  onerror(res) {}

  /**
   * 
   */
  onmessage(res) {}

  /**
   * 
   */
  send(data) {
    uni.sendSocketMessage({
      data: data,
    });
  }

  /**
   * 
   */
  close() {
    uni.closeSocket();
  }
}

export default WebSocketPolyfill;